html, body, .body {
        width: 100%;
        height: 100%;
        margin: 0;
}

body {
  margin: 0px;
  padding: 0px;
  font-family: 'Lato', sans-serif;
/*  background: #2580bd;*/
  color: white;
  background: rgba(11,139,224,1);
background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(11,139,224,1)), color-stop(100%, rgba(37,128,189,1)));
background: -webkit-linear-gradient(top, rgba(11,139,224,1) 0%, rgba(37,128,189,1) 100%);
background: -o-linear-gradient(top, rgba(11,139,224,1) 0%, rgba(37,128,189,1) 100%);
background: -webkit-gradient(linear, left top, left bottom, from(rgba(11,139,224,1)), to(rgba(37,128,189,1)));
background: linear-gradient(to bottom, rgba(11,139,224,1) 0%, rgba(37,128,189,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0b8be0', endColorstr='#2580bd', GradientType=0 );
}

