/*  BLACK AND WHITE EFFECT
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
*/

.Video {
  text-align: center;
  border-style: solid;
  border-radius: 100px;
  /* Put distance between them */
  margin-right: 0.5em;
  margin-left: 0.5em;
  border-width: 1px;
  border-color: #494949;
  /* Make black and white */
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  display: inline-block;
}



.userMessage {
  display: block;
  overflow-wrap: break-word;
  /* background: -webkit-linear-gradient(right, #eee, #333); */
  background: -webkit-linear-gradient(right, #1089d9, #df2424, #ffffff);
  background: -webkit-gradient(linear, left top, right top, from(#1387d5), color-stop(15%, #fff));
  background: -webkit-linear-gradient(left, #1387d5, #fff 15%);
  background: -o-linear-gradient(left, #1387d5, #fff 15%);
  background: linear-gradient(to right, #1387d5, #fff 15%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
  margin-bottom: 1.4em;
}

#root {
  height: 100%;
  width: 100%; 
}


